            <div class="p8">
                杨医师揭秘：皮肤顽癣久治不愈的三大原因——“毒邪”除不尽，皮肤屏障功能确实，人体阳气低下，癣病难以断根！
            </div>
            <p>
                为什么皮癣医院看不好？西药吃不好？激光打不掉？中药不见效？杨医师认为，毒是皮肤病的本，乱是皮肤病的标。有毒而疏导则治，无毒不守正则乱。像 牛皮癣，白癜风，皮炎湿疹，顽固杂癣等，就是湿毒，风毒，热毒等毒素在体内的淤积。所以说，“毒素”淤积了，身体排毒功能受阻了，是形成皮肤病的根本原 因，一要祛毒，二还要保证外边毒邪进不来，三是要扶助人体正气，增强人体消解毒邪的能力，只有这样，才能斩草除根，从源头上遏止病菌，毒邪的再次入侵，皮 肤病才不会复发。
            </p>
            <div class="p8">
                杨医师独门绝技：内擀毒，外修肤，中扶正，“风邪湿毒”驱除干净，皮肤顽癣自然痊愈。
            </div>
            <p>
                传 统方法，都是想方设法从体内把毒素、毒邪“拔”出来，但是很多毒素卡在骨头缝里、表皮组织深处，从外边往出拔一定会留下死角。只有把毒素向内赶，赶到血液 里面，然后通过血液循环排除，皮肤正常张合，才能排毒彻底，不留死角，康复皮肤病。治疗皮肤病，就是调理身体，一靠疏导，将毒素引流到体外；二靠改良人体 内环境。杨医师的独门绝技“内赶毒，外修肤，中扶正”三大步骤，轻松拔掉发病基础，让皮肤病不再犯。<br/>
◎内赶毒：净肤止痒祛屑，立竿见影。遵 循苗医“赶毒法”治病原则，杨医师特制赶毒鲜药方中的高科技小分子成分，迅速杀灭皮肤表层的细菌，立刻止痒祛屑；在清洁皮肤的基础上，药物瞬间通过真皮 层，渗透到皮肤下层4000微米处，赶走神经、血管、淋巴、肌肉等组织的毒素，通过体液交换及血液内循环代谢分解排出体外，迅速起效，彻底赶毒。<br/>
◎外修肤：清除底层毒素，消斑散淤。遵循苗医古老的“滚鸡蛋技法”整治原则，涂抹特制药膏，药物成分持久深入到皮肤最里层，先修复深层的皮下组织，彻底清除淤积毒素，斩草除根；同时药物作用由内而外，恢复皮肤真皮层的宣泄功能，让皮肤自由张合呼吸，祛腐生肌，消斑散淤，换肤生新，干净康肤。<br/>
◎中扶正：扶助正气平衡，不再反复。杨 医师特别配制苗家鲜药，能够扶助人体正气，平衡气血阴阳，调理脏腑功能，改良人体内环境，激发人体自身免疫潜力，重筑皮肤免疫系统，让皮肤病不再反复。石 老爷子：“这个方子有多好？我说一个事实，大家就知道了！咱们侗族深处贵州十万大山深处，地理环境恶劣，潮湿最为明显，村民的衣服、棉被都要用风扇吹干， 蚊虫、蛇蚁随处可见，很多细菌传播导致村民身上经常出现一些红斑、血点疹以及各种奇癣，越痒越挠，越挠越厉害，全身散布，但一旦用了这个治癣妙方，这些症 状很快就消失，而且不再复发！”<br/>
